<template>
  <div class="content">
    <div class="md-layout">
      <div class="md-layout-item md-size-15">
        <md-button class="md-success" @click="active=true">Thm</md-button>
      </div>
      <div class="md-layout-item md-size-30">
        <md-field>
          <label>Tm kim</label>
          <md-input v-model="searchContent.textSearch" @change="searchInputChange"></md-input>
        </md-field>
      </div>
      <div class="md-layout-item md-size-20">
        <md-field>
          <label for="phanloai">Phn loi</label>
          <md-select v-model="searchContent.category">
            <md-option value="name">Theo tn</md-option>
            <md-option value="card">Theo CMND</md-option>
          </md-select>
        </md-field>
      </div>
    </div>
    <div class="md-layout">
      <div class="md-layout-item md-medium-size-100 md-xsmall-size-100 md-size-100">
        <md-card>
          <md-card-header data-background-color="green">
            <h4 class="title">Danh sch ti x</h4>
            <p class="category">Bng hin th thng tin ti x</p>
          </md-card-header>
          <md-card-content>
            <md-table v-model="drivers">
              <md-table-row
                slot="md-table-row"
                slot-scope="{ item }"
                md-auto-select
                md-selectable="multiple"
              >
                <md-table-cell md-label="Tn ti x ">{{ item.dr_name }}</md-table-cell>
                <md-table-cell md-label="CMND">{{ item.dr_card }}</md-table-cell>
                <!-- <md-table-cell md-label="nh">
                  <img style="width:30px;height:60px" class="driver_avatar" :src="item.dr_avatar" />
                </md-table-cell>-->
                <md-table-cell md-label="Ngy sinh">{{ item.dr_card }}</md-table-cell>
                <md-table-cell md-label="Cp bc ">{{ item.dr_rank }}</md-table-cell>
                <md-table-cell md-label="Thao tc">
                  <md-button
                    class="md-raised edit_btn"
                    @click="editDialog = true;selectedRow = item"
                  >Sa</md-button>
                  <md-button
                    class="md-raised delete_btn"
                    @click="activeDeleteDialog=true;selectedRow=item"
                  >Xa</md-button>
                </md-table-cell>
              </md-table-row>
            </md-table>
          </md-card-content>
        </md-card>
      </div>
    </div>
    <md-dialog :md-active.sync="active">
      <md-dialog-title>Thm thng tin xe</md-dialog-title>
      <md-content>
        <div>
          <md-field>
            <label>Tn ti x</label>
            <md-input v-model="input.dr_name"></md-input>
          </md-field>
        </div>
        <div>
          <md-field>
            <label>Ngy sinh</label>
            <md-input v-model="input.dr_birthday"></md-input>
          </md-field>
        </div>
        <!-- <md-field>
          <label>nh </label>
          <md-file v-model="fileImage" ref="file"  @md-change="onfileChange($event)" />
        </md-field>-->
        <div>
          <md-field>
            <label>Cp bc</label>
            <md-input v-model="input.dr_rank"></md-input>
          </md-field>
        </div>
        <div>
          <md-field>
            <label>CMND</label>
            <md-input v-model="input.dr_card"></md-input>
          </md-field>
        </div>
      </md-content>
      <md-dialog-actions>
        <md-button class="md-primary" @click="active=false">Hy</md-button>
        <md-button class="md-primary" @click="submit();active=false">Lu</md-button>
      </md-dialog-actions>
    </md-dialog>
    <md-dialog :md-active.sync="editDialog">
      <md-dialog-title>Sa thng tin xe</md-dialog-title>
      <md-content>
        <div>
          <md-field>
            <label>Tn ti x</label>
            <md-input v-model="edit.dr_name" :placeholder="selectedRow.dr_name"></md-input>
          </md-field>
        </div>
        <div>
          <md-field>
            <label>Ngy sinh</label>
            <md-input v-model="edit.dr_birthday" :placeholder="selectedRow.dr_birthday"></md-input>
          </md-field>
        </div>
        <div>
          <md-field>
            <label>Cp bc</label>
            <md-input v-model="edit.dr_rank" :placeholder="selectedRow.dr_rank"></md-input>
          </md-field>
        </div>
        <div>
          <md-field>
            <label>CMNDc</label>
            <md-input v-model="edit.dr_card" :placeholder="selectedRow.dr_rank"></md-input>
          </md-field>
        </div>
      </md-content>
      <md-dialog-actions>
        <md-button class="md-primary" @click="editDialog=false">Hy</md-button>
        <md-button class="md-primary" @click="update();editDialog=false">Lu</md-button>
      </md-dialog-actions>
    </md-dialog>
    <md-dialog-confirm
      :md-active.sync="activeDeleteDialog"
      md-title="Xa?"
      md-content="Bn c chc chn mun xa hay khng?"
      md-confirm-text="ng "
      md-cancel-text="Hy"
      @md-confirm="onConfirmDelete"
    />
  </div>
</template>

<script>
import {
  getAllDriver,
  insertDriver,
  deleteDriver,
  updateDriver
} from "../api/driver";
export default {
  name: "driverPage",
  components: {},
  data() {
    return {
      searchContent: { textSearch: "", category: "" },
      drivers: [],
      fileImage: "",
      edit: {
        dr_rank: "",
        dr_name: "",
        dr_birthday: "",
        dr_card: ""
      },
      input: {
        dr_rank: "",
        dr_name: "",
        dr_birthday: "",
        dr_card: ""
      },
      active: false,
      name: "",
      selectedRow: {},
      activeDeleteDialog: false,
      editDialog: false
    };
  },
  methods: {
    searchInputChange() {
      if (this.searchContent.category === "") {
        this.$message({
          type: "warning",
          message: "Bn vui lng chn phn loi trc khi tm kim!"
        });
        this.searchContent.textSearch = "";
      } else {
        switch (this.searchContent.category) {
          case "name":
            getAllDriver().then(result => {
              this.drivers = [];
              this.drivers = result.data.filter(
                x => x.dr_name == this.searchContent.textSearch
              );
            });
            break;
          case "card":
            getAllDriver().then(result => {
              this.drivers = [];
              this.drivers = result.data.filter(
                x => x.dr_card == this.searchContent.textSearch
              );
            });
            break;
        }
      }
    },
    onfileChange() {
      //
    },
    resetForm() {
      this.edit.dr_name = "";
      this.edit.dr_rank = "";
      this.edit.dr_birthday = "";
    },
    onConfirmDelete() {
      deleteDriver(this.selectedRow)
        .then(result => {
          if (result.status === 200) {
            this.$message({ type: "success", message: "Sa thnh cng" });
          }
        })
        .then(() => {
          this.resetForm();
          this.getList();
        });
    },
    update() {
      console.log(this.edit);
      updateDriver({ ...this.edit, _id: this.selectedRow._id })
        .then(result => {
          if (result.status === 200) {
            this.$message({ type: "success", message: "Sa thnh cng" });
          }
        })
        .then(() => {
          this.resetForm();
          this.getList();
        });
    },
    getList() {
      this.drivers = [];
      getAllDriver().then(result => {
        this.drivers = result.data;
      });
    },
    submit() {
      insertDriver(this.input)
        .then(result => {
          if (result.status === 200) {
            this.$message({ type: "success", message: "Thm thnh cng" });
          }
        })
        .then(() => {
          this.getList();
        });
    }
  },
  mounted() {
    getAllDriver().then(result => {
      this.drivers = result.data;
    });
  }
};
</script>
<style lang="scss" scoped>
.driver_avatar {
  width: 100px !important;
}
.delete_btn {
  background: #ff5252 !important;
}
.edit_btn {
  background: #448aff !important;
}
.md-dialog {
  min-width: 50%;
}
</style>
